import {connect} from "react-redux";
import {Dispatch} from "redux";
import {CounterStateType, RootStateType} from "../../state/redux-store";
import {
    maxValueSetterAC, onFocusAC, onIncrementAC, onResetAC, onSetScreenMessageAC, onSetSettingsAC,
    onSettingsModeTogglerAC, startValueSetterAC
} from "../../state/counter-reducer";
import {CounterWithRedux} from "./CounterWithRedux";


type MapDispatchToPropsType = {
    onIcrement: () => void,
    onReset: () => void,
    onSettingsModeToggler: (settingsMode: boolean) => void,
    onSetScreenMessage: (screenMessage: string) => void,
    maxValueSetter: (maxValue: number) => void,
    startValueSetter: (startValue: number) => void,
    onSetSettings: () => void,
    onFocus: () => void
}

let mapStateToProps = (state: RootStateType): CounterStateType => {
    return {
        maxCounter: state.counterPage.maxCounter,
        startCounter: state.counterPage.startCounter,
        currentCounter: state.counterPage.currentCounter,
        screenMessage: state.counterPage.screenMessage,
        maxError: state.counterPage.maxError,
        startError: state.counterPage.startError,
        settingsMode: state.counterPage.settingsMode,
        maxHelperText: state.counterPage.maxHelperText,
        startHelperText: state.counterPage.startHelperText
    }
}
let mapDispatchToProps = (dispatch: Dispatch): MapDispatchToPropsType => {
    return {
        onIcrement: () => {
            dispatch(onIncrementAC())
        },
        onReset: () => {
            dispatch(onResetAC())
        },
        onSettingsModeToggler: (settingsMode) => {
            dispatch(onSettingsModeTogglerAC(settingsMode))
        },
        onSetScreenMessage: (screenMessage) => {
            dispatch(onSetScreenMessageAC(screenMessage))
        },
        maxValueSetter: (maxValue) => {
            dispatch(maxValueSetterAC(maxValue))
        },
        startValueSetter: (startValue) => {
            dispatch(startValueSetterAC(startValue))
        },
        onSetSettings: () => {
            dispatch(onSetSettingsAC())
        },
        onFocus: () => {
            dispatch(onFocusAC())
        }
    }
}
export const CounterContainer = connect(mapStateToProps, mapDispatchToProps)(CounterWithRedux)